import Vue from 'vue'
import Router from 'vue-router'
//import home from '../pages/Home'

Vue.use(Router)

export default new Router({
  mode:'history',
  base:'m',
  routes: [

    {
      path: '/',
      name: 'Home',
      //component: Home
      component: (resolve) => require(['../pages/Home'], resolve)
    },
    // {
    //   path: '/',
    //   name: 'test',
    //   //component: Home
    //   component: (resolve) => require(['../pages/MP_verify_MVxPyPibe3G0lAQO.txt'], resolve)
    // },
    {
      path: '/setform',
      name: 'SetForm',
      component: (resolve) => require(['../pages/SetForm'], resolve)
    },
    {
      path: '/order',
      name: 'Order',
      component: (resolve) => require(['../pages/Order'], resolve)
    },
    {
      path: '/wait',
      name: 'Wait',
      component: (resolve) => require(['../pages/Wait'], resolve)
    },
    {
      path: '/refund',
      name: 'Refund',
      component: (resolve) => require(['../pages/Refund'], resolve)
    },
    {
      path: '/lookcert',
      name: 'LookCert',
      component: (resolve) => require(['../pages/LookCert'], resolve)
    },


  ]
})



